import {keys} from './urls';
import dialog from '../views/v-dialogs/changelog';
import page from '../views/v-page/changelog';
import upload from '../views/v-uploader/changelog';

let changeLog = {};
changeLog[keys.vDialogs] = dialog;
changeLog[keys.vPage] = page;
changeLog[keys.vUploader] = upload;

export default changeLog;


