<template>
    <div class="container">
        <h2 class="page-header">Install</h2>
        <p>use <strong>npm</strong> to install plugin</p>
        <pre class="brush:bash">
            npm i v-uploader --save
        </pre>
        <p>Include plugin in your <code>main.js</code> file</p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vUploader from 'v-uploader';

            // v-uploader plugin global config
            const uploaderConfig = () => {
                return {
                    // file uploader server url
                    uploadFileUrl: 'http://xxx/upload/publicFileUpload',
                    // file delete server url
                    deleteFileUrl: 'http://xxx/upload/deleteUploadFile',
                    // set the way to show upload message(upload fail message)
                    showMessage: (vue, message) => {
                        //using v-dialogs to show message
                        vue.$vDialog.alert(message, null, {messageType: 'error'});
                    }
                }
            };

            // install plugin with params
            Vue.use(vUploader, uploaderConfig);
        </pre>
        <p>there is using <a href="https://github.com/TerryZ/v-dialogs" target="_blank">v-dialogs</a> to show message in example</p>

        <h2 class="page-header">Deploy on your component</h2>
        <p>template code</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
              <!-- v-bind 'setting' data to config file uploader -->
              <!-- bind event 'done' to receive file uploaded info -->
              <v-uploader :setting="uploadConfig" @done="uploadDone" ></v-uploader>
            </template>
        ]]>
        </script>

        <p>script code</p>
        <pre class="brush:js">
        export default {
            name: 'myComponent',
            data(){
                return {
                    uploadConfig: {
                        multiple: false,
                        language: 'en'//default: 'cn'
                    }
                }
            },
            methods:{
                //receive file uploaded info
                //@type Array
                uploadDone(files){
                    if(files && Array.isArray(files) && files.length){
                        // do something...
                    }
                }
            }
        };
        </pre>

        <h2 class="page-header">Example</h2>

        <h3>single file mode</h3>
        <!--<v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>-->

        <h3>multiple file mode</h3>
        <v-uploader :setting="multipleConfig" @done="uploadDone" ></v-uploader>



        <h2 class="page-header">Options</h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th>name</th>
                    <th>type</th>
                    <th>description</th>
                    <th>required</th>
                    <th>default</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>totalRow</td>
                    <td>number</td>
                    <td>the total number of records in query<br>in parent component, you need update totalRow number when it's chnage</td>
                    <td>true</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>language</td>
                    <td>string</td>
                    <td>
                        the page bar language
                        <ul>
                            <li><code>'cn'</code></li>
                            <li><code>'en'</code></li>
                            <li><code>'jp'</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>'cn'</td>
                </tr>
                <tr>
                    <td>pageSizeMenu</td>
                    <td>Array</td>
                    <td>page length menu list</td>
                    <td>false</td>
                    <td>[10, 20, 50, 100]</td>
                </tr>
                <tr>
                    <td>align</td>
                    <td>string</td>
                    <td>
                        the page bar alignment direction
                        <ul>
                            <li><code>'left'</code></li>
                            <li><code>'center'</code></li>
                            <li><code>'right'</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>'right'</td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header">Events</h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th>name</th>
                <th>description</th>
                <th>optionalPayload</th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>page-change</td>
                <td>when page change or page length change, plugin will emit an event to the parent</td>
                <td>
                    <ul>
                        <li><code>pageNumber</code><br>current page number</li>
                        <li><code>pageSize</code><br>the total number of records in one page</li>
                    </ul>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "Demo",
        data(){
            let arr = [];
            for (let i = 0; i < 108; i++) {
                arr.push(i+1);
            }
            return {
                singleConfig: {
                    language: 'en'
                },
                multipleConfig: {
                    multiple: true,
                    language: 'en'
                }
            };
        },
        methods: {
            uploadDone(files){
                if(files && Array.isArray(files) && files.length){
                    console.log(files);
                }
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>
    div.photo-wall {
        div.brick {
            border: 1px solid #DDDDDD;
            border-radius: 2px;
            background-color: #F4F4F4;
            text-align: center;
            line-height: 100%;
            float: left;
            margin: 0 10px 10px 0;
            width: 180px;
            height: 130px;
            font-size: 60px;
            color: #BBBBBB;
            padding-top: 35px;
        }
    }
</style>


